import React, { Component } from 'react';
import '../../css/style.css'
import Divider from 'material-ui/Divider';
import BottomNavigation from 'material-ui/BottomNavigation';
import MdRowing from 'react-icons/lib/md/rowing'
import Button from 'material-ui/Button'
import { withStyles } from 'material-ui/styles';
import Card, { CardContent, CardMedia } from 'material-ui/Card';
import Typography from 'material-ui/Typography';
import p1 from '../../images/p1.jpg'
import p2 from '../../images/p2.jpg'
import p3 from '../../images/p3.jpg'

const styles = {
    card: {
        maxWidth: 345,
        height: '100%'
    },
    media: {
        height: 200,
    },
};


class UnregisteredApp extends Component {
    constructor(props) {
        super(props);
        this.state = {
            value: 0
        }
    }
    handleChange = (event, value) => {
        this.setState({ value });
    };
    render() {
        const { value } = this.state;
        const { classes } = this.props;
        return (
            <main className='main_wrapper_unregistered'>
                <div className='super row no-margin fixed full-width between-xs transparent main_header' >
                    <div className='col-lg-4' >
                        <BottomNavigation value={value} onChange={this.handleChange} className='full-width transparent full-height'>
                            <BottomNavigation
                                value={value}
                                onChange={this.handleChange}
                                showLabels
                                className='full-width transparent full-height'
                            >
                                <Button className='menu_link transparent'><a className='gray_text' href="">Servicios</a></Button>
                                <Button className='menu_link transparent'><a className='gray_text' href="">Paquetes</a></Button>
                                <Button className='menu_link transparent'><a className='gray_text' href="">Precios</a></Button>
                            </BottomNavigation>
                        </BottomNavigation>
                    </div>
                    <div className='col-lg end-xs' >
                        <a href="/" className='new_button_primary ln-100'>Iniciar sesión</a>
                    </div>
                    <div className='col-lg-2 main_title end-xs' >
                        <h2 className='smart_title ln-100 no-margin gray_text' >SmartBreak  <span className='icon_button' ><MdRowing size={30} /></span></h2>
                    </div>
                </div>
                <div className='row no-margin full-height-vh main_unregistered_app section_welcome' >
                    <div className='col-lg-12 no-padding' >
                        <div className='row center-xs full-width no-margin' >
                            <div className='col-lg-12 no-padding' >
                                <div className='row no-margin center-xs title_content' >
                                    <div className='col-lg-5' >
                                        <div className='main_title' >
                                            <h1 className='gray_text' >Construye estrategias inteligentes para tu escuela</h1>
                                            <h3 className='gray_text' >Todas lo que necesitas saber en la palma de tu mano.</h3>
                                            <Divider light />
                                        </div>
                                    </div>
                                    <div className='smart_margin' ></div>
                                    <div className='row no-margin around-xs full-width' >
                                        <div className='col-lg-4' >
                                            <Card className='full-width'>
                                                <CardMedia
                                                    className={classes.media}
                                                    image={p1}
                                                    title="Contemplative Reptile"
                                                />
                                                <CardContent>
                                                    <Typography type="headline" component="h2">
                                                        Estadisicas
                                                    </Typography>
                                                    <Typography component="p">
                                                        Nuestras estadisticas muestran en tiempo real los datos que en tu escuela son más
                                                        importantes
                                                    </Typography>
                                                </CardContent>
                                            </Card>
                                        </div>
                                        <div className='col-lg-4' >
                                            <Card className='full-width'>
                                                <CardMedia
                                                    className={classes.media}
                                                    image={p2}
                                                    title="Contemplative Reptile"
                                                />
                                                <CardContent>
                                                    <Typography type="headline" component="h2">
                                                        Examenes
                                                    </Typography>
                                                    <Typography component="p">
                                                        La manera más inteligente de evaluar.
                                                    </Typography>
                                                    <Typography component="p">
                                                        De maestros para maestros.
                                                    </Typography>
                                                </CardContent>
                                            </Card>
                                        </div>
                                        <div className='col-lg-4' >
                                            <Card className='full-width'>
                                                <CardMedia
                                                    className={classes.media}
                                                    image={p3}
                                                    title="Contemplative Reptile"
                                                />
                                                <CardContent>
                                                    <Typography type="headline" component="h2">
                                                        Estadisicas
                                                    </Typography>
                                                    <Typography component="p">
                                                        Nuestras estadisticas muestran en tiempo real los datos que en tu escuela son más
                                                        importantes
                                                    </Typography>
                                                </CardContent>
                                            </Card>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className='row no-margin full-height-vh' >
                    <div className='col-lg-12' >
                        Faltan 2 que 3 cosillas, tranqui
                    </div>
                </div>
                <div className='row no-margin fixed learn_more_button' >
                    <div className='col-xs-12' >
                        Conoce más
                    </div>
                </div>
            </main>
        )
    }
}

export default withStyles(styles)(UnregisteredApp);



